<template>
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl" placeholder="">
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"> 
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input type="file" id="customFile" class="form-control" ref="fileInput" @change="uploadFild">
              </div>
              <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
              <!--  -->
              <div class="mt-5" v-if="tempProduct.imagesUrl">
                <div v-for="(image, index) in tempProduct.imagesUrl" :key="index" class="mb-3 input-group" >
                  <input type="url" class="form-control form-control" placeholder="" v-model="tempProduct.imagesUrl[index]">
                  <button type="button" class="btn btn-outline-danger" @click="tempProduct.imagesUrl.splice(index, 1)"></button>
                </div>
                <div v-if="tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1] || !tempProduct.imagesUrl.length">
                  <button class="btn btn-outline-primary btn-sm d-block w-100" @click="tempProduct.imagesUrl.push('')"></button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input type="text" class="form-control" id="title" v-model="tempProduct.title" placeholder="">
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label"></label>
                  <input type="text" class="form-control" id="category" v-model="tempProduct.category" placeholder="">
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input type="text" class="form-control" id="unit" v-model="tempProduct.unit" placeholder="">
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label"></label>
                  <input type="number" class="form-control" id="origin_price" v-model.number="tempProduct.origin_price" placeholder="">
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input type="number" class="form-control" id="price" v-model.number="tempProduct.price" placeholder="">
                </div>
              </div>
              <hr>

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea type="text" class="form-control" id="description" v-model="tempProduct.description" placeholder=""></textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea type="text" class="form-control" id="content" v-model="tempProduct.content" placeholder=""></textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" :true-value="1" :false-value="0" id="is_enabled" v-model="tempProduct.is_enabled">
                  <label class="form-check-label" for="is_enabled"></label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal"></button>
          <button type="button" class="btn btn-primary" @click="$emit('update-product', tempProduct)"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'

export default {
  // 
  props: {
    product: {
      // 
      type: Object,
      // 
      default () {
        return {}
      }
    }
  },
  watch: {
    product () {
      this.tempProduct = this.product
    }
  },
  // 
  data () {
    return {
      //  modal
      modal: {},
      // 
      tempProduct: {}
    }
  },
  methods: {
    showModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    },
    uploadFild () {
      // 
      const uploadedFild = this.$refs.fileInput.files[0]
      // console.dir(uploadedFild)
      //  formData 
      const formData = new FormData()
      //  ( input name  )
      formData.append('file-to-upload', uploadedFild)
      //  API 
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      this.$http.post(url, formData).then((res) => {
        // console.log(res.data)
        if (res.data.success) {
          this.tempProduct.imageUrl = res.data.imageUrl
        }
      })
    }
  },
  // 
  mounted () {
    this.modal = new Modal(this.$refs.modal)
  }
}
</script>
