<template>
  <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3">
    <h1 class="h2"></h1>
    <div class="text-end">
      <button class="btn btn-primary" type="button" @click="openModal(true)"></button>
    </div>
  </div>

    <table class="table table-striped mt-4">
        <thead>
            <tr>
                <th width="120"></th>
                <th></th>
                <th width="120"></th>
                <th width="120"></th>
                <th width="100"></th>
                <th width="200"></th>
            </tr>
        </thead>
        <tbody class="table-group-divider">
            <tr v-for="item in products" :key="item.id">
                <td>{{ item.category }}</td>
                <td>{{ item.title }}</td>
                <td class="text-right">{{ item.origin_price }}</td>
                <td class="text-right">{{ item.price }}</td>
                <td>
                    <span class="text-success" v-if="item.is_enabled"></span>
                    <span class="text-muted" v-else></span>
                </td>
                <td>
                    <div class="btn-group">
                    <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
                    <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)"></button>
                    </div>
                </td>
            </tr>
        </tbody>
    </table>
    <ProductModal ref="productModal" :product="tempProduct" @update-product="updateProduct"></ProductModal>
    <DelModal ref="delModal" :item="tempProduct" @del-item="delProduct"></DelModal>
</template>

<script>
import ProductModal from '../components/ProductModal.vue'
import DelModal from '../components/DelModal.vue'

export default {
  data () {
    return {
      //  API []
      products: [],
      //  API []
      pagination: {},
      // 
      tempProduct: {},
      // 
      isNew: false
    }
  },
  // 
  components: {
    ProductModal,
    DelModal
  },
  methods: {
    // 
    getProducts () {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products`
      this.$http.get(api).then((res) => {
        if (res.data.success) {
          // console.log(res.data)
          this.products = res.data.products
          this.pagination = res.data.pagination
        }
      })
    },
    //  Modal
    openModal (isNew, item) {
      // console.log(isNew, item)
      if (isNew) {
        this.tempProduct = {}
      } else {
        this.tempProduct = { ...item }
      }
      this.isNew = isNew
      const productComponent = this.$refs.productModal
      productComponent.showModal()
    },
    // 
    updateProduct (item) {
      this.tempProduct = item
      // 
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
      let httpMethod = 'post'

      // 
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
        httpMethod = 'put'
      }

      const productComponent = this.$refs.productModal
      this.$http[httpMethod](api, { data: this.tempProduct }).then((res) => {
        console.log(res)
        productComponent.hideModal()
        // 
        this.getProducts()
      })
    },
    //  Delete Modal
    openDelProductModal (item) {
      // eslint-disable-next-line no-undef
      this.tempProduct = { ...item }
      const delComponent = this.$refs.delModal
      delComponent.showModal()
    },
    // 
    delProduct () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`
      this.$http.delete(url).then((res) => {
        // console.log(res.data)
        const delComponent = this.$refs.delModal
        delComponent.hideModal()
        this.getProducts()
      })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
